div.card-wrapper {
	background-color: white;
	border: 1px solid #797979;

	width: 5rem;
	padding: 0.75rem;

	border-radius: 0.5rem;
	margin: 0.15rem;
	margin-top: 0.5rem;
}

h2.card-title {
	margin: 0;
}

div.card__row {
	display: flex;
	align-items: center;
	justify-content: space-between;
}